import React from 'react';
import { Button, Modal, Form, InputGroup, Col } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class FeesModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Fees
                        </Modal.Title>
                    </Modal.Header>

                    <Form  noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="FeeName">
                                    <Form.Label>Fee Name</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Fee Name"
                                        value={this.props.FEE_NAME} required
                                        onChange={this.props.changeFeeName}
                                        name="FeeName"
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please select Fee Name.
                                 </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="FeeShortName">
                                    <Form.Label>Fee Short Name</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Fee Short Name"
                                        value={this.props.FEE_SHORTNAME} required
                                        onChange={this.props.changeFeeShortName}
                                        name="FeeShortName"
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please select Fee Short Name.
                                 </Form.Control.Feedback>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="FeeValue">
                                    <Form.Label>Fee Value</Form.Label>
                                    <Form.Control type="number" placeholder="Enter Fee Value"
                                        value={this.props.FEE_VALUE} required
                                        onChange={this.props.changeFeeValue}
                                        name="FeeValue"
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please select Fee Value.
                                 </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="Priority">
                                    <Form.Label>Priority</Form.Label>
                                    <Form.Control as="select" value={this.props.PRIORITY} required type="number" placeholder="Select Value"
                                        onChange={this.props.changePriority} name="Priority">
                                        {/* <option>Select</option> */}
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                    </Form.Control>
                                    <Form.Control.Feedback type="invalid">
                                        Please select Priority.
                                 </Form.Control.Feedback>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="ChargeOnPOS">
                                    <Form.Label>Charge On POS</Form.Label>
                                    <Form controlId="sample">
                                        <Form.Check inline
                                            type="radio"
                                            label="Yes"
                                            name="ChargeOnPOSYes"
                                            value="1"
                                            checked={this.props.CHARGE_ON_POS === 1}
                                            onChange={this.props.changeChargeOnPOSYes}
                                        />
                                        <Form.Check inline
                                            type="radio"
                                            label="No"
                                            name="ChargeOnPOSNo"
                                            value="0"
                                            checked={this.props.CHARGE_ON_POS === 0}
                                            onChange={this.props.changeChargeOnPOSNo}
                                        />
                                    </Form>
                                </Form.Group>
                                <Form.Group as={Col} md="6" id="formGridCheckbox" controlId="HAS_PERMIT">
                                    <Form.Label>Has Permit</Form.Label>
                                    <Form.Check
                                        type="switch"
                                        id="custom-switch"
                                        label=""
                                        name="HasPermit"
                                        checked={this.props.HAS_PERMIT === 1 ? true : false}
                                        onChange={this.props.changeHasPermit}
                                    />
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="DepartmentName">
                                    <Form.Label>Department Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.DEPARTMENT_NAME}
                                            placeholder="Search Department Name"
                                            aria-describedby="inputGroupPrepend"
                                            name="DepartmentName"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm"
                                                onClick={this.props.handleDepartmentSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Department Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="ModuleName">
                                    <Form.Label>Module Name</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.MODULE_NAME}
                                            placeholder="Search Module Name"
                                            aria-describedby="inputGroupPrepend"
                                            name="ModuleName"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleModuleSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Module Name.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="PermitTypeName">
                                    <Form.Label>Permit Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.PERMIT_TYPE_NAME}
                                            placeholder="Search Permit Type"
                                            aria-describedby="inputGroupPrepend"
                                            ame="PermitTypeName"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handlePermitTypeSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Permit Type.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="FeeCategoryCode">
                                    <Form.Label>Fee Category</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.FEECATEGORY_CODE}
                                            placeholder="Search Fee Category"
                                            aria-describedby="inputGroupPrepend"
                                            name="FeeCategoryCode"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleFeeCategorySearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Fee Category.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="COADescription">
                                    <Form.Label>Chart of Account</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.COA_DESCRIPTION}
                                            placeholder="Search Chart of Account"
                                            aria-describedby="inputGroupPrepend"
                                            name="COADescription"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleCOASearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select Chart of Account.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>

                                <Form.Group as={Col} md="6" controlId="COACategoryCode">
                                    <Form.Label>COA Category Code</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.COACATEGORY_CODE}
                                            placeholder="Search COA Category Code"
                                            aria-describedby="inputGroupPrepend"
                                            name="COACategoryCode"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleCOACategorySearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select COA Category Code.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="COASubCategoryCode">
                                    <Form.Label>COA Sub Category Code</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            value={this.props.COASUBCATEGORY_CODE}
                                            placeholder="Search COA SubCategory Code"
                                            aria-describedby="inputGroupPrepend"
                                            name="COASubCategoryCode"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleCOASubCategorySearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select COA Sub Category Code.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control
                                            type="text"
                                            name="SystemStatus"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please select status.
                                 </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Fees
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.FEE_NAME} onChange={this.props.FEE_NAME} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default FeesModal;