    populateData() {
        const getdata = `
        query {
            Fees  {
                FEE_ID,
                FEE_ID
                FEE_SHORTNAME
                FEE_VALUE
                FEE_NAME
                PRIORITY
                ACTIVE
                CHARGE_ON_POS
                HAS_PERMIT
                DEPARTMENT_ID:SET_DEPARTMENT_ID
                SET_DEPARTMENTS{
                        DEPARTMENT_ID,
                        DEPARTMENT_NAME
                                    },
                MODULE_ID: SET_MODULE_ID
                Set_Modules{
                        MODULE_ID,
                        MODULE_NAME
                        },
                FEECATEGORY_ID:  SET_DEPT_FEECATEGORY_ID
                FeeCategory{
                        FEECATEGORY_ID,
                        FEECATEGORY_CODE
                        }
                COA_ID:  SET_COA_ID
                SET_CHART_OF_ACCOUNTS{
                        COA_ID,
                        COA_DESCRIPTION
                                },
                COACATEGORY_ID:  SET_COACATEGORY_ID
                COACategory{
                                COACATEGORY_ID
                                COACATEGORY_CODE
                                }
                COASUBCATEGORY_ID:  SET_COASUBCATEGORY_ID
                COASubCategory{
                                COASUBCATEGORY_ID
                                COASUBCATEGORY_CODE
                                }
                PERMIT_TYPE_ID
                PermitTypes{
                PERMIT_TYPE_ID
                PERMIT_TYPE_NAME
                }
                SYS_STATUS_ID
                SYSTEMSTATUS{
                            SYS_STATUS_ID,
                            SYS_STATUS_NAME
                            },
                CREATEDBY
                MODIFIEDBY
                CREATED_DATE
                MODIFIED_DATE                                                                                   
              }
          }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.Fees
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

