    handleSubmit = (event) => {
        event.preventDefault();
        
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            const form = `
        mutation{
            addFee(
                FEE_SHORTNAME:"${this.state.FEE_SHORTNAME}",
                FEE_VALUE:${this.state.FEE_VALUE},
                FEE_NAME:"${this.state.FEE_NAME}",
                PRIORITY:${this.state.PRIORITY},
                ACTIVE:${this.state.ACTIVE},
                CHARGE_ON_POS:${this.state.CHARGE_ON_POS},
                HAS_PERMIT:${this.state.HAS_PERMIT},
                SET_COA_ID:${this.state.COA_ID},
                SET_COACATEGORY_ID:${this.state.COACATEGORY_ID},
                 SET_COASUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID},
                SET_DEPT_FEECATEGORY_ID:${this.state.FEECATEGORY_ID},
                PERMIT_TYPE_ID:${this.state.PERMIT_TYPE_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_MODULE_ID:${this.state.MODULE_ID},
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    FEE_ID
            }
        }
        `;
            
            const formEdit = `
        mutation{
            updateFee(
                FEE_ID:${this.state.FEE_ID},
                FEE_SHORTNAME:"${this.state.FEE_SHORTNAME}",
                FEE_VALUE:${this.state.FEE_VALUE},
                FEE_NAME:"${this.state.FEE_NAME}",
                PRIORITY:${this.state.PRIORITY},
                ACTIVE:${this.state.ACTIVE},
                CHARGE_ON_POS:${this.state.CHARGE_ON_POS},
                HAS_PERMIT:${this.state.HAS_PERMIT},
                SET_COA_ID:${this.state.COA_ID},
                SET_COACATEGORY_ID:${this.state.COACATEGORY_ID},
                 SET_COASUBCATEGORY_ID:${this.state.COASUBCATEGORY_ID},
                SET_DEPT_FEECATEGORY_ID:${this.state.FEECATEGORY_ID},
                PERMIT_TYPE_ID:${this.state.PERMIT_TYPE_ID},
                SET_DEPARTMENT_ID:${this.state.DEPARTMENT_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                SET_MODULE_ID:${this.state.MODULE_ID},
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    FEE_ID
            }
        }
        `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

